




import { Component, Vue } from 'vue-property-decorator';
import * as THREE from 'three';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader';

@Component
export default class App extends Vue {

  private cameraDistance: number = 100;
  private near: number = 0.1;
  private far: number = 1000.0;
  private fogColor: number = 0xeeeeee;
  private clearColor: number = 0xeeeeee;

  private canvas!: HTMLCanvasElement;
  private context!: WebGLRenderingContext;
  private scene3D!: THREE.Scene;
  private scene2D!: THREE.Scene;
  private camera3D!: THREE.PerspectiveCamera;
  private camera2D!: THREE.OrthographicCamera;
  private renderer3D!: THREE.WebGLRenderer;

  private controls!: OrbitControls;
  private logo!: THREE.Sprite;

  private gltfHander!: () => void;
  private animate!: () => void;

  // public beforeCreate() {
  //   console.log('beforeCreate');
  // }

  // public constructor() {
  //   super();
  //   console.log('constructor');
  // }

  // public created() {
  //   console.log('created');
  // }

  // public beforeMount() {
  //   console.log('beforeMount');
  // }

  // public mounted() {
  //   console.log('mounted');
  // }

  // public beforeUpdate() {
  //   console.log('beforeUpdate');
  // }

  // public updated() {
  //   console.log('updated');
  // }

  // public beforeDestroy() {
  //   console.log('beforeDestroy');
  // }

  // public destroyed() {
  //   console.log('destroyed');
  // }

  public mounted() {
    this.canvas = document.getElementById('canvas') as HTMLCanvasElement;
    this.context = this.canvas.getContext('webgl') as WebGLRenderingContext;
    if (!this.context) {
      return;
    }

    this.cameraDistance = Math.max(window.innerWidth, window.innerHeight);

    this.init();
  }

  private init() {
    this.initScene();
    this.initCamera();
    this.initRender();
    this.initLight();
    this.initControl();
    this.initGltf();
    this.initUI();

    this.initWindowResizeEvent();
    this.initAnimate();
  }

  private initStats() {
    // let stats = new Stats();
    // document.body.appendChild(stats.dom);
  }

  private initScene() {
    this.scene3D = new THREE.Scene();
    // this.scene3D.fog = new THREE.Fog(this.fogColor, this.near, this.far);

    this.scene2D = new THREE.Scene();
  }

  private initCamera() {
    const fov: number = this.calcFov(this.cameraDistance, window.innerWidth, window.innerWidth / window.innerHeight);
    this.camera3D = new THREE.PerspectiveCamera(fov, window.innerWidth / window.innerHeight, this.near, this.far);
    this.camera3D.position.set(0, this.cameraDistance / 4 * 3, this.cameraDistance);
    // this.camera3D.position.set(0, 0, this.cameraDistance);
    this.camera3D.lookAt(new THREE.Vector3(0, 0, 0));
    this.camera3D.updateProjectionMatrix();

    this.camera2D = new THREE.OrthographicCamera(0, window.innerWidth, window.innerHeight, 0, this.near, this.far);
    // this.camera2D.position.set(0, this.cameraDistance / 4 * 3, this.cameraDistance);
    this.camera2D.position.set(0, 0, this.cameraDistance);
    this.camera2D.lookAt(new THREE.Vector3(0, 0, 0));
    this.camera2D.updateProjectionMatrix();
  }

  private initRender() {
    this.renderer3D = new THREE.WebGLRenderer({
      canvas: this.canvas,
      context: this.context,
      antialias: true,
      powerPreference: 'high-performance',
    });
    // console.log(renderer3D.extensions.get());
    this.renderer3D.autoClearColor = false;
    this.renderer3D.setClearColor(this.clearColor, 1.0);
    this.renderer3D.setSize(window.innerWidth, window.innerHeight, true);
    this.renderer3D.setPixelRatio(window.devicePixelRatio);
    this.renderer3D.shadowMap.enabled = true;
    this.renderer3D.shadowMap.type = THREE.PCFSoftShadowMap;
    document.body.appendChild(this.renderer3D.domElement);
  }

  private initWindowResizeEvent() {
    window.onresize = () => {
      this.camera3D.fov = this.calcFov(this.cameraDistance, window.innerWidth, window.innerWidth / window.innerHeight);
      this.camera3D.aspect = window.innerWidth / window.innerHeight;
      this.camera3D.updateProjectionMatrix();

      this.camera2D.left = 0;
      this.camera2D.right = window.innerWidth;
      this.camera2D.top = window.innerHeight;
      this.camera2D.bottom = 0;
      this.camera2D.updateProjectionMatrix();

      if (this.logo && this.logo.material && this.logo.material.map) {
        this.logo.position.set(
          window.innerWidth * 0.05 + this.logo.material.map.image.height / 2,
          window.innerHeight * 0.95 - this.logo.material.map.image.height / 2,
          1);
      }
      this.renderer3D.setSize(window.innerWidth, window.innerHeight, true);
    };
  }

  private initLight() {
    const ambientLight = new THREE.AmbientLight(0xeeeeee, 0.75);
    this.scene3D.add(ambientLight);

    // const directionalLight = new THREE.DirectionalLight(0xeeeeee, 1.0);
    // directionalLight.castShadow = true;
    // directionalLight.shadow.mapSize.width = 512;
    // directionalLight.shadow.mapSize.height = 512;
    // directionalLight.shadow.camera.near = 0.1;
    // directionalLight.shadow.camera.far = 1000.0;
    // directionalLight.position.set(3, 4, 0);
    // this.scene3D.add(directionalLight);

    // const rectLight = new THREE.RectAreaLight(0xeeeeee, 1.0, 100, 100);
    // rectLight.position.set(100 * 4 / 3, 100, 0);
    // rectLight.lookAt(0, 0, 0);
    // this.scene3D.add(rectLight);

    const pointLight = new THREE.PointLight(0xeeeeee, 1.0, 1000, 1.5);
    pointLight.castShadow = true;
    pointLight.shadow.mapSize.width = 512;
    pointLight.shadow.mapSize.height = 512;
    pointLight.shadow.camera.near = 0.1;
    pointLight.shadow.camera.far = 1000.0;
    pointLight.position.set(100, 400, 300);
    this.scene3D.add(pointLight);
  }

  private initControl() {
    this.controls = new OrbitControls(this.camera3D, this.renderer3D.domElement);
    // controls.enabled = true;
    this.controls.minDistance = 200;
    this.controls.maxDistance = 800;
    // this.controls.minZoom = 1;
    // this.controls.maxZoom = 5;
    this.controls.minPolarAngle = -Math.PI * 2; // Math.PI / 6;
    this.controls.maxPolarAngle = Math.PI * 2; // Math.PI / 3;
    this.controls.minAzimuthAngle = -Math.PI * 2;
    this.controls.maxAzimuthAngle = Math.PI * 2;
    // 使动画循环使用时阻尼或自转 意思是否有惯性
    // this.controls.enableDamping = true;
    // 动态阻尼系数 就是鼠标拖拽旋转灵敏度
    // this.controls.dampingFactor = 0.05;
    // this.controls.enableZoom = true;
    this.controls.zoomSpeed = 2;
    // this.controls.enableRotate = true;
    this.controls.rotateSpeed = 1;
    // this.controls.enablePan = true;
    this.controls.panSpeed = 1;
    this.controls.autoRotate = true;
    this.controls.autoRotateSpeed = 1;

    // 如果使用animate方法时，将此函数删除
    // controls.addEventListener( 'change', render );
  }

  private initGltf() {
    // Instantiate a loader
    const loader = new GLTFLoader();
    // // Optional: Provide a DRACOLoader instance to decode compressed mesh data
    // THREE.DRACOLoader.setDecoderPath("./libs/draco/");
    // loader.setDRACOLoader(new THREE.DRACOLoader());
    // // Optional: Pre-fetch Draco WASM/JS module, to save time while parsing.
    // THREE.DRACOLoader.getDecoderModule();

    const self = this;
    // Load a glTF resource
    loader.load(
      // resource URL
      // './models/gltf/desktop.glb',
      // './models/gltf/shuichi.glb',
      './models/gltf/scene.gltf',
      // called when the resource is loaded
      (res) => {
        const gltf = res.scene;
        gltf.scale.set(30, 30, 30);
        for (const child of gltf.children) {
          // console.log(child);
          if (child.name === 'RootNode_(gltf_orientation_matrix)') {
            child.castShadow = true;
            child.receiveShadow = true;
          }
        }

        // for (const child of gltf.children) {
        //   console.log(child);
        //   if (child.name === 'Plane') {
        //     child.castShadow = false;
        //     child.receiveShadow = true;
        //   }
        //   if (child.name === 'Cube') {
        //     child.castShadow = true;
        //     child.receiveShadow = true;
        //   }
        // }

        self.scene3D.add(gltf);
        // console.log(res);
        // console.log(renderer3D.info)

        self.gltfHander = () => {
            // gltf.rotation.x += 0.01;
            // gltf.rotation.y += 0.01;
        };
      },
      (xhr) => {
        console.log((xhr.loaded / xhr.total) * 100 + '% loaded');
      },
      (error) => {
        console.log('An error happened');
      },
    );
  }

  private initUI() {
    const texture = new THREE.TextureLoader();
    const self = this;
    texture.load(
      './images/jiji.png',
      (res) => {
        res.magFilter = THREE.NearestFilter;
        res.minFilter = THREE.NearestFilter;
        res.anisotropy = 4;
        const spriteMaterial = new THREE.SpriteMaterial({
          map: res,
          transparent: true,
          precision: 'highp',
          side: THREE.DoubleSide,
        });
        const width = res.image.width;
        const height = res.image.height;

        self.logo = new THREE.Sprite(spriteMaterial);
        self.logo.scale.set(width, height, 1);
        self.logo.position.set(
          window.innerWidth * 0.05 + width / 2,
          window.innerHeight * 0.95 - height / 2,
          1);

        self.scene2D.add(self.logo);
      },
      (xhr) => {
        console.log((xhr.loaded / xhr.total) * 100 + '% loaded');
      },
      (error) => {
        console.log('An error happened');
      },
    );
  }

  private initAnimate() {
    const self = this;
    this.animate = () => {
      requestAnimationFrame(self.animate);
      // self.stats.begin();
      // // monitored code goes here
      // self.stats.end();

      if (self.gltfHander) {
          self.gltfHander();
      }
      self.controls.update();
      self.renderer3D.clear();
      self.renderer3D.render(self.scene3D, self.camera3D);
      // self.renderer3D.clearDepth();
      self.renderer3D.render(self.scene2D, self.camera2D);
    };
    this.animate();
  }

  private calcFov(distance: number, width: number, radius: number): number {
    if (radius < 1) {
      width = width / radius;
    }
    return Math.atan(width / 2 / distance) * 2 * (180 / Math.PI);
  }
}
